.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #ffffff; /* Light gray background, similar to the screenshot */
    height: 100vh;
  }
  
  .location-form {
    background-color: white; /* Form background */
    padding: 25px;
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); /* Soft shadow */
    max-width: 500px;
    width: 100%;
  }
  
  .form-title {
    font-size: 1.5em;
    color: #007bff; /* Blue color similar to the header in the screenshot */
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333; /* Dark text color */
  }
  
  .input-field {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #f0f4f8; /* Light background for inputs */
  }
  
  .input-field:focus {
    border-color: #007bff; /* Blue border on focus */
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Soft blue glow on focus */
  }
  
  textarea {
    height: 100px;
    resize: none;
  }
  
  .submit-button {
    width: 100%;
    padding: 12px;
    font-size: 1em;
    background-color: #007bff; /* Blue button similar to the screenshot */
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submit-button:hover {
    background-color: #0056b3; /* Darker blue on hover */
  }
  
  @media (max-width: 600px) {
    .location-form {
      padding: 15px;
    }
  
    .form-title {
      font-size: 1.2em;
    }
  }
  