.gate-form-page {
    display: flex;
    min-height: 100vh;
  }
  
  .gate-form-container {
    margin-left: 350px; /* Space for the sidebar */
    padding: 20px;
    background-color: #f0f4f8;
    border-radius: 8px;
    max-width: 600px;
    width: 100%;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  }
  
  h3 {
    color: #333;
  }
  
  .add-gate-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 15px;
    margin-bottom: 15px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .add-gate-button:hover {
    background-color: #0056b3;
  }
  
  .gate-form {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
  }
  
  .form-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  h4 {
    margin: 0;
    font-size: 1.2em;
    color: #333;
  }
  
  .delete-button {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .delete-button:hover {
    background-color: #cc0000;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: #333;
  }
  
  .input-field {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #f0f4f8;
  }
  
  .input-field:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  }
  
  .save-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px;
    width: 100%;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .save-button:hover {
    background-color: #0056b3;
  }
  