  .sidebar {
    width: 250px;
    height: 100vh;
    background-color: #f0f4f8;
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  }
  
  .sidebar-logo {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .logo {
    width: 40px;
    height: 40px;
  }
  
  .sidebar-menu {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .menu-item {
    display: flex;
    align-items: center;
    padding: 10px;
    font-size: 1em;
    color: #333;
    cursor: pointer;
    border-radius: 8px;
    margin-bottom: 10px;
    transition: background-color 0.3s ease;
  }
  
  .menu-item:hover {
    background-color: #d9e9ff;
  }
  
  .menu-icon {
    margin-right: 15px;
    font-size: 1.2em;
  }
  
  .menu-text {
    font-size: 1em;
  }
  
  .menu-item.active {
    background-color: #007bff;
    color: white;
  }
  